s limited to ~80 characters */\n.measure-wide {\n  max-width: $measure-wide;\n}\n\n/* Measure is limited to ~45 characters */\n.measure-narrow {\n  max-width: $measure-narrow;\n}\n\n/* Book paragraph style - paragraphs are indented with no vertical spacing. */\n.indent {\n  text-indent: 1em;\n  margin-top: 0;\n  margin-bottom: 0;\n}\n\n.small-caps {\n  font-variant: small-caps;\n}\n\n/* Combine this class with a width to truncate text (or just leave as is to truncate at width of containing element. */\n\n.truncate {\n  white-space: nowrap;\n  overflow: hidden;\n  text-overflow: ellipsis;\n}\n\n@media #{$breakpoint-not-small} {\n  .measure-ns  {\n    max-width: $measure;\n  }\n  .measure-wide-ns {\n    max-width: $measure-wide;\n  }\n  .measure-narrow-ns {\n    max-width: $measure-narrow;\n  }\n  .indent-ns {\n    text-indent: 1em;\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n  .small-caps-ns {\n    font-variant: small-caps;\n  }\n  .truncate-ns {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n}\n\n@media #{$breakpoint-medium} {\n  .measure-m {\n    max-width: $measure;\n  }\n  .measure-wide-m {\n    max-width: $measure-wide;\n  }\n  .measure-narrow-m {\n    max-width: $measure-narrow;\n  }\n  .indent-m {\n    text-indent: 1em;\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n  .small-caps-m {\n    font-variant: small-caps;\n  }\n  .truncate-m {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n}\n\n@media #{$breakpoint-large} {\n  .measure-l {\n    max-width: $measure;\n  }\n  .measure-wide-l {\n    max-width: $measure-wide;\n  }\n  .measure-narrow-l {\n    max-width: $measure-narrow;\n  }\n  .indent-l {\n    text-indent: 1em;\n    margin-top: 0;\n    margin-bottom: 0;\n  }\n  .small-caps-l {\n    font-variant: small-caps;\n  }\n  .truncate-l {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n  }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   UTILITIES\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n/* Equivalent to .overflow-y-scroll */\n.overflow-container {\n  overflow-y: scroll;\n}\n\n.center {\n  margin-right: auto;\n  margin-left: auto;\n}\n\n.mr-auto { margin-right: auto; }\n.ml-auto { margin-left:  auto; }\n\n@media #{$breakpoint-not-small}{\n  .center-ns {\n    margin-right: auto;\n    margin-left: auto;\n  }\n  .mr-auto-ns { margin-right: auto; }\n  .ml-auto-ns { margin-left:  auto; }\n}\n\n@media #{$breakpoint-medium}{\n  .center-m {\n    margin-right: auto;\n    margin-left: auto;\n  }\n  .mr-auto-m { margin-right: auto; }\n  .ml-auto-m { margin-left:  auto; }\n}\n\n@media #{$breakpoint-large}{\n  .center-l {\n    margin-right: auto;\n    margin-left: auto;\n  }\n  .mr-auto-l { margin-right: auto; }\n  .ml-auto-l { margin-left:  auto; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   VISIBILITY\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n\n/*\n    Text that is hidden but accessible\n    Ref: http://snook.ca/archives/html_and_css/hiding-content-for-accessibility\n*/\n\n.clip {\n  position: fixed !important;\n  _position: absolute !important;\n  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n  clip: rect(1px, 1px, 1px, 1px);\n}\n\n@media #{$breakpoint-not-small} {\n  .clip-ns {\n    position: fixed !important;\n    _position: absolute !important;\n    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n    clip: rect(1px, 1px, 1px, 1px);\n  }\n}\n\n@media #{$breakpoint-medium} {\n  .clip-m {\n    position: fixed !important;\n    _position: absolute !important;\n    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n    clip: rect(1px, 1px, 1px, 1px);\n  }\n}\n\n@media #{$breakpoint-large} {\n  .clip-l {\n    position: fixed !important;\n    _position: absolute !important;\n    clip: rect(1px 1px 1px 1px); /* IE6, IE7 */\n    clip: rect(1px, 1px, 1px, 1px);\n  }\n}\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   WHITE SPACE\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n\n.ws-normal { white-space: normal; }\n.nowrap { white-space: nowrap; }\n.pre { white-space: pre; }\n\n@media #{$breakpoint-not-small} {\n  .ws-normal-ns { white-space: normal; }\n  .nowrap-ns { white-space: nowrap; }\n  .pre-ns { white-space: pre; }\n}\n\n@media #{$breakpoint-medium} {\n  .ws-normal-m { white-space: normal; }\n  .nowrap-m { white-space: nowrap; }\n  .pre-m { white-space: pre; }\n}\n\n@media #{$breakpoint-large} {\n  .ws-normal-l { white-space: normal; }\n  .nowrap-l { white-space: nowrap; }\n  .pre-l { white-space: pre; }\n}\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n   VERTICAL ALIGN\n\n   Media Query Extensions:\n     -ns = not-small\n     -m  = medium\n     -l  = large\n\n*/\n\n.v-base     { vertical-align: baseline; }\n.v-mid      { vertical-align: middle; }\n.v-top      { vertical-align: top; }\n.v-btm      { vertical-align: bottom; }\n\n@media #{$breakpoint-not-small} {\n  .v-base-ns     { vertical-align: baseline; }\n  .v-mid-ns      { vertical-align: middle; }\n  .v-top-ns      { vertical-align: top; }\n  .v-btm-ns      { vertical-align: bottom; }\n}\n\n@media #{$breakpoint-medium} {\n  .v-base-m     { vertical-align: baseline; }\n  .v-mid-m      { vertical-align: middle; }\n  .v-top-m      { vertical-align: top; }\n  .v-btm-m      { vertical-align: bottom; }\n}\n\n@media #{$breakpoint-large} {\n  .v-base-l     { vertical-align: baseline; }\n  .v-mid-l      { vertical-align: middle; }\n  .v-top-l      { vertical-align: top; }\n  .v-btm-l      { vertical-align: bottom; }\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n  HOVER EFFECTS\n  Docs: http://tachyons.io/docs/themes/hovers/\n\n    - Dim\n    - Glow\n    - Hide Child\n    - Underline text\n    - Grow\n    - Pointer\n    - Shadow\n\n*/\n\n/*\n\n  Dim element on hover by adding the dim class.\n\n*/\n.dim {\n  opacity: 1;\n  transition: opacity .15s ease-in;\n}\n.dim:hover,\n.dim:focus {\n  opacity: .5;\n  transition: opacity .15s ease-in;\n}\n.dim:active {\n  opacity: .8; transition: opacity .15s ease-out;\n}\n\n/*\n\n  Animate opacity to 100% on hover by adding the glow class.\n\n*/\n.glow {\n  transition: opacity .15s ease-in;\n}\n.glow:hover,\n.glow:focus {\n  opacity: 1;\n  transition: opacity .15s ease-in;\n}\n\n/*\n\n  Hide child & reveal on hover:\n\n  Put the hide-child class on a parent element and any nested element with the\n  child class will be hidden and displayed on hover or focus.\n\n  <div class=\"hide-child\">\n    <div class=\"child\"> Hidden until hover or focus </div>\n    <div class=\"child\"> Hidden until hover or focus </div>\n    <div class=\"child\"> Hidden until hover or focus </div>\n    <div class=\"child\"> Hidden until hover or focus </div>\n  </div>\n*/\n\n.hide-child .child {\n  opacity: 0;\n  transition: opacity .15s ease-in;\n}\n.hide-child:hover  .child,\n.hide-child:focus  .child,\n.hide-child:active .child {\n  opacity: 1;\n  transition: opacity .15s ease-in;\n}\n\n.underline-hover:hover,\n.underline-hover:focus {\n  text-decoration: underline;\n}\n\n/* Can combine this with overflow-hidden to make background images grow on hover\n * even if you are using background-size: cover */\n\n.grow {\n  -moz-osx-font-smoothing: grayscale;\n  backface-visibility: hidden;\n  transform: translateZ(0);\n  transition: transform 0.25s ease-out;\n}\n\n.grow:hover,\n.grow:focus {\n  transform: scale(1.05);\n}\n\n.grow:active {\n  transform: scale(.90);\n}\n\n.grow-large {\n  -moz-osx-font-smoothing: grayscale;\n  backface-visibility: hidden;\n  transform: translateZ(0);\n  transition: transform .25s ease-in-out;\n}\n\n.grow-large:hover,\n.grow-large:focus {\n  transform: scale(1.2);\n}\n\n.grow-large:active {\n  transform: scale(.95);\n}\n\n/* Add pointer on hover */\n\n.pointer:hover {\n  cursor: pointer;\n}\n\n/*\n   Add shadow on hover.\n\n   Performant box-shadow animation pattern from\n   http://tobiasahlin.com/blog/how-to-animate-box-shadow/\n*/\n\n.shadow-hover {\n  cursor: pointer;\n  position: relative;\n  transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);\n}\n\n.shadow-hover::after {\n  content: '';\n  box-shadow: 0px 0px 16px 2px rgba( 0, 0, 0, .2 );\n  border-radius: inherit;\n  opacity: 0;\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: -1;\n  transition: opacity 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);\n}\n\n.shadow-hover:hover::after,\n.shadow-hover:focus::after {\n  opacity: 1;\n}\n\n/* Combine with classes in skins and skins-pseudo for\n * many different transition possibilities. */\n\n.bg-animate,\n.bg-animate:hover,\n.bg-animate:focus {\n  transition: background-color .15s ease-in-out;\n}\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n  Z-INDEX\n\n  Base\n    z = z-index\n\n  Modifiers\n    -0 = literal value 0\n    -1 = literal value 1\n    -2 = literal value 2\n    -3 = literal value 3\n    -4 = literal value 4\n    -5 = literal value 5\n    -999 = literal value 999\n    -9999 = literal value 9999\n\n    -max = largest accepted z-index value as integer\n\n    -inherit = string value inherit\n    -initial = string value initial\n    -unset = string value unset\n\n  MDN: https://developer.mozilla.org/en/docs/Web/CSS/z-index\n  Spec: http://www.w3.org/TR/CSS2/zindex.html\n  Articles:\n    https://philipwalton.com/articles/what-no-one-told-you-about-z-index/\n\n  Tips on extending:\n  There might be a time worth using negative z-index values.\n  Or if you are using tachyons with another project, you might need to\n  adjust these values to suit your needs.\n\n*/\n\n.z-0 { z-index: 0; }\n.z-1 { z-index: 1; }\n.z-2 { z-index: 2; }\n.z-3 { z-index: 3; }\n.z-4 { z-index: 4; }\n.z-5 { z-index: 5; }\n\n.z-999 { z-index: 999; }\n.z-9999 { z-index: 9999; }\n\n.z-max {\n  z-index: 2147483647;\n}\n\n.z-inherit { z-index: inherit; }\n.z-initial { z-index: initial; }\n.z-unset { z-index: unset; }\n\n","\n// Converted Variables\n\n\n// Custom Media Query Variables\n\n\n/*\n\n    NESTED\n    Tachyons module for styling nested elements\n    that are generated by a cms.\n\n*/\n\n.nested-copy-line-height p,\n.nested-copy-line-height ul,\n.nested-copy-line-height ol {\n  line-height: $line-height-copy;\n}\n\n.nested-headline-line-height h1,\n.nested-headline-line-height h2,\n.nested-headline-line-height h3,\n.nested-headline-line-height h4,\n.nested-headline-line-height h5,\n.nested-headline-line-height h6 {\n  line-height: $line-height-title;\n}\n\n.nested-list-reset ul,\n.nested-list-reset ol {\n  padding-left: 0;\n  margin-left: 0;\n  list-style-type: none;\n}\n\n.nested-copy-indent p+p {\n  text-indent: $letter-spacing-1;\n  margin-top: $spacing-none;\n  margin-bottom: $spacing-none;\n}\n\n.nested-copy-seperator p+p {\n  margin-top: $spacing-copy-separator;\n}\n\n.nested-img img {\n  width: 100%;\n  max-width: 100%;\n  display: block;\n}\n\n.nested-links a {\n  color: $blue;\n  transition: color .15s ease-in;\n}\n\n.nested-links a:hover,\n.nested-links a:focus {\n  color: $light-blue;\n  transition: color .15s ease-in;\n}\n",".wrapper\n{\n    width: 100%;\n    max-width: 1460px;\n    margin: 0 auto;\n    padding: 0 20px;\n    box-sizing: border-box;\n}\n\n.opblock-tag-section\n{\n    display: flex;\n    flex-direction: column;\n}\n\n.try-out.btn-group {\n    padding: 0;\n    display: flex;\n    flex: 0.1 2 auto;\n}\n\n.try-out__btn {\n    margin-left: 1.25rem;\n}\n\n.opblock-tag\n{\n    display: flex;\n    align-items: center;\n\n    padding: 10px 20px 10px 10px;\n\n    cursor: pointer;\n    transition: all .2s;\n\n    border-bottom: 1px solid rgba($opblock-tag-border-bottom-color, .3);\n\n    &:hover\n    {\n        background: rgba($opblock-tag-background-color-hover,.02);\n    }\n}\n\n@mixin method($c